<template>
  <div class="g-header">
    <span class="icon icon-remote-sensing-logo" style="line-height: 68px; float:left; font-size: 36px;"></span>
    <span style="vertical-align: top; line-height: 68px; padding-top: 12px; display:inline-block; margin-left: 1rem;">
      <h2>CCAS</h2>
      <p>Estimating the Threshold of Vegetation Productivity</p>
    </span>
    <ul style="line-height: 68px; height: 68px; display:inline-block;" class="menu">
      <li>
        <a href="#">Scenario Analysis</a>
      </li>
      <li>
        <a href="#">Comparative Analysis</a>
      </li>
    </ul>
    <select style="margin-left: 10rem;">
      <option value="a">General</option>
      <option value="a">General</option>
      <option value="a">General</option>
      <option value="a">General</option>
    </select>
    <select>
      <option value="a">RCP2P6</option>
      <option value="a">RCP2P6</option>
      <option value="a">RCP2P6</option>
      <option value="a">RCP2P6</option>
    </select>
    <select>
      <option value="a">gfdl-esm2m</option>
      <option value="a">gfdl-esm2m</option>
      <option value="a">gfdl-esm2m</option>
      <option value="a">gfdl-esm2m</option>
    </select>
    <span style="float: right; line-height: 68px; font-size: 24px;" class="icon icon-full-screen"></span>
  </div>
</template>
<script>
export default {
  name: 'gHeader'
}
</script>
<style lang="scss" scoped>
.g-header {
  padding: 0 1rem;
  color: white;
  background-color: #7b0002;
  box-shadow: 0 0 10px #7b0002;
  border-bottom: #7b0002 - #333 1px solid;
  z-index: 2000;
  height: 68px;
}
h2,
p {
  margin: 0;
  line-height: 1;
}

h2 {
  font-size: 22px;
  margin-bottom: 4px;
}

p {
  font-size: 12px;
}

.menu {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 6.8rem;
  li {
    float: left;

    a {
      text-decoration: none;
      color: white;
      line-height: 1em;
      display: inline-block;
      height: 68px;
      font-size: 14px;
      text-align: center;
      line-height: 1.5;
      padding-top: 0.75rem;
      width: 7rem;
      border-right: rgba(255, 255, 255, 0.3) 1px solid;
      box-sizing: border-box;
      font-weight: bolder;
    }

    &:first-child a {
      border-left: rgba(255, 255, 255, 0.3) 1px solid;
    }
  }
}

select {
  width: 175px;
  border: none;
  vertical-align: top;
  display: inline-block;
  margin-top: 1.35rem;
  line-height: 1.75rem;
  height: 1.75rem;
  padding: 0 1rem;
  font-size: 14px;
  margin-right: 1rem;

  &:focus{
    outline: none;
  }
}
</style>



