<template>
  <div id="app">
    <g-header></g-header>
    <div id="map">
      <div class="time-line">
        <div class="el-icon-arrow-left step-button"></div>
        <div class="time-line-main">
          <el-steps :space="200" :active="4" finish-status="success" align-center>
            <el-step title="1990"></el-step>
            <el-step title="1991"></el-step>
            <el-step title="1992"></el-step>
            <el-step title="1993"></el-step>
            <el-step title="1994"></el-step>
            <el-step title="1995"></el-step>
            <el-step title="1996"></el-step>
          </el-steps>
        </div>
        <div class="el-icon-arrow-right step-button"></div>
        <div class="step-button">
          <span class="el-icon-caret-right play-button"></span>
        </div>
      </div>
      <slide-panel id="slide-panel"></slide-panel>
      <div id="toggle-button">
        <span class="el-icon-d-arrow-left"></span>
      </div>
    </div>
  </div>
</template>
<script>
import GHeader from './components/GHeader'
import SlidePanel from './components/SlidePanel'
import ol from 'openlayers'
import axios from 'axios'
import areas from '../json/LPct_wgs84.json'
import * as turf from '@turf/turf'

window.ol = ol

var features = (new ol.format.EsriJSON()).readFeatures(areas)
var countries = (new ol.format.GeoJSON()).writeFeatures(features)
countries = JSON.parse(countries)

export default {
  name: 'app',
  components: {
    GHeader,
    SlidePanel
  },
  methods: {
    initMap: function () {
      var map = new ol.Map({
        view: new ol.View({
          center: [108.2, 37.5],
          zoom: 7,
          projection: 'EPSG:4326'
        }),
        target: 'map',
        layers: [
          new ol.layer.Tile({
            source: new ol.source.TileArcGISRest({
              url: 'http://services.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer'
            })
          })
        ]
      })

      var _layer = null

      function addImage () {
        axios.get('http://219.244.65.144:16227/ccas/tiff?colormap=[[0,[0,0,255,255]],[250,[0,255,255,255]],[500,[0,255,0,255]],[750,[255,255,0,255]],[1000,[255,0,0,255]]]&f=/home/CAAS/Result/admin/cae447e0-0835-49f1-9e80-78f49a4707ef/LPCC/General/General/GPP/LoessPlateau/1km/year/1982-2012/LPCC_General_General_GPP_LoessPlateau_1km_year_2012.tif').then(function (res) {
          var extent = res.data.extent
          var url = 'http://219.244.65.144:16227/ccas/out/' + res.data.fileName

          var minPoint = turf.point([extent[0], extent[1]])
          var maxPoint = turf.point([extent[2], extent[3]])

          extent = minPoint.geometry.coordinates.concat(maxPoint.geometry.coordinates)

          var layer = new ol.layer.Image({
            source: new ol.source.ImageStatic({
              url: url,
              imageExtent: extent
            })
          })

          map.addLayer(layer)

          _layer = new ol.layer.Vector({
            source: new ol.source.Vector({
              features: (new ol.format.GeoJSON()).readFeatures(countries)
            }),
            style: function () {
              return new ol.style.Style({
                fill: new ol.style.Fill({
                  color: 'rgba(0,0,0,0)'
                })
              })
            }
          })
          map.addLayer(_layer)

          map.on('pointermove', function (e) {
            var res = map.getFeaturesAtPixel(e.pixel)

            if (res) {
              var name = res[0].get('NAME')
              _layer.setStyle(function (feature) {
                return new ol.style.Style({
                  fill: new ol.style.Fill({
                    color: feature.get('NAME') === name ? 'rgba(123, 0, 2, 0.8)' : 'rgba(0, 0, 0, 0)'
                  }),
                  stroke: new ol.style.Stroke({
                    color: feature.get('NAME') === name ? 'rgba(123, 0, 2, 1)' : 'rgba(0, 0, 0, 0)',
                    width: 2
                  })
                })
              })
            } else {
              _layer.setStyle(function (feature) {
                return new ol.style.Style({
                  fill: new ol.style.Fill({
                    color: 'rgba(0, 0, 0, 0)'
                  })
                })
              })
            }
          })
        })
      }

      // addImage()
    }
  },
  mounted: function () {
    this.initMap()
  }
}
</script>
<style lang="scss">
html,
body,
#app {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
#map {
  position: absolute;
  top: 68px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.time-line {
  position: absolute;
  top: 0.25rem;
  right: -2.5rem;
  z-index: 200;

  &>*{
    float: left;
  }

  .time-line-main{
    display: block;
    width: 35rem;
    background-color: rgba(255, 255, 255, 0.8);
    height: 4rem;
    padding-top: 0.75rem;
    box-sizing: border-box;
  }

  .step-button{
    background-color: rgba(255, 255, 255, 0.8);
    height: 4rem;
    line-height: 4rem;
    font-size: 32px;
    color: #999;
  }

  .play-button{
    background-color: #ececea;
    color: #666666;
    border: 1px #666666 solid;
    border-radius: 100%;
    margin: 0.2rem;
  }

  &::before {
    float: left;
    content: "";
    display: block;
    height: 4rem;
    width: 6rem;
    border-right: rgba(255, 255, 255, 0.8) 3rem solid;
    border-top: transparent 2rem solid;
    border-bottom: transparent 2rem solid;
    box-sizing: border-box;
  }

  &::after {
    float: left;
    content: "";
    display: block;
    height: 4rem;
    width: 6rem;
    border-left: rgba(255, 255, 255, 0.8) 3rem solid;
    border-top: transparent 2rem solid;
    border-bottom: transparent 2rem solid;
    box-sizing: border-box;
    border-right: none;
  }
}

.el-step__head.is-success .el-step__icon {
  background-color: #a40000;
}

#slide-panel {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 200;
  width: 30rem;
}

#toggle-button {
  position: absolute;
  left: 30.25rem;
  top: 0.25rem;
  z-index: 200;
  background-color: white;
  padding: 0.5rem;
  font-size: 14px;
  width: 1.5rem;
  text-align: center;
  font-size: 16px;
}

.el-form-item__label {
  font-size: 12px;
}
</style>



